*::-webkit-scrollbar-track {
    box-shadow: inset 0 0 0 6px #fdefd0;
    border-radius: 8px;
}

*::-webkit-scrollbar-thumb {
    background: #edce9e;
    border-radius: 8px;
}

*::-webkit-scrollbar {
    width: 4px;
    height: 4px;
}

*::-webkit-scrollbar:hover {
    width: 4px;
    height: 4px;
}

*:hover::-webkit-scrollbar-track {
    box-shadow: inset 0 0 0 1px #fdefd0, inset 0 0 0 6px #fdefd0;
}

*:hover::-webkit-scrollbar-thumb {
    background: #edce9e;
}

