.feature-code {
    display: flex;
}

.code-section {
    width: 100%;
    padding: 8px 0 8px 8px;
    overflow: hidden;
}

.code-container {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: calc(490px);
    overflow: hidden;
    position: relative;
}

.code-block {
    font-size: 12px;
    display: flex;
    -webkit-box-align: stretch;
    align-items: stretch;
    -webkit-box-flex: 1;
    flex-grow: 1;
    position: relative;
    overflow: hidden auto;
}

.code-block>span {
    font-size: 12px;
    /* background: rgb(50, 41, 49); */
    color: rgb(185, 181, 184);
    display: flex;
    line-height: 1.42857;
    white-space: pre;
    padding: 8px 0 8px 8px;
    width: 100%;
    font-weight: 700;
    flex-direction: column;
    justify-content: space-between;
}

.code-block code {
    line-height: 1.66667;
    padding: 8px;
    overflow-x: auto;
    white-space: pre;
    width: 100%;
    font-weight: 700;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 5px;
    opacity: .4;
    /* background-color: rgb(50, 41, 49); */
    background-color: #0e0f11;
    height: 105px;
    border-radius: 8px;
    font-family: monospace, monospace;
    font-size: 14px;
}

.code-block code.active {
    opacity: 1;
}

.code-block code .token-wrapper {
    opacity: 1;
    font-size: inherit;
    line-height: 1.42857;
    /* color: rgb(185, 181, 184); */
    color: #f8f8f2;
    /* background-color: rgb(50, 41, 49); */
    background-color: #0e0f11;
    flex-shrink: 0;
    padding: 0 8px;
    user-select: text;
    gap: 5px;
    white-space: normal;
    width: 100%;
}

.code-block code .token-wrapper.indented {
    margin-left: 20px;
}

.code-block code .token-wrapper.no-wrap {
    white-space: nowrap;
}

code .token-wrapper .token {
    font-weight: bold;
    opacity: .6;
    user-select: text;
}

code .token-wrapper .token.bright {
    opacity: 1;
}

code .token-wrapper .token.bracket {
    color: #FDD600;
}

code .token-wrapper .token.package {
    color: #4EC9B0;
    font-weight: bolder;
}

code .token-wrapper .token.keyword {
    color: #C586C0;
}

code .token-wrapper .token.function {
    color: #DCDCA9;
}

code .token-wrapper .token.string {
    color: #CE9178;
}

code .token-wrapper .token.number {
    color: #B5CEA4;
}

code .token-wrapper .token.comment {
    color: #6A9955;
}

code .token-wrapper .token.txt {
    color: #9CDCFE;
    font-weight: bolder;
}

.tab-container {
    padding: 8px 8px 8px 0;
}

.tab-container .features {
    display: flex;
    flex-direction: column;
    width: 100%;
    padding: 8px 8px 8px 0;
    height: calc(490px);
    max-width: 100vw;
    -webkit-box-align: stretch;
    align-items: stretch;
    overflow: visible auto;
    -webkit-mask: none;
    mask: none;
    justify-content: space-between;
    font-family: Georgia, serif;
}

.tab-container .features .feature-wrapper {
    display: flex;
    flex-direction: row;
    width: 100%;
    -webkit-box-pack: justify;
    justify-content: space-between;
    border-radius: 8px;
    position: relative;
    overflow: hidden;
    background-color: rgb(255, 255, 255);
    border: 1px solid rgb(255, 255, 255);
}

.tab-container .features .feature-wrapper.active {
    /* border: 1px solid rgb(96, 25, 200);
    background-color: rgb(247, 241, 253); */
    border: 1px solid #7c6553;
    background-color: #fcf0d1;
}

.feature-wrapper .feature {
    display: flex;
    flex-direction: column;
    padding: 8px;
    width: 100%;
    transition: all 0.2s ease-in-out 0s;
    opacity: 0.65;
    cursor: pointer;
    border-radius: 0px;
    -webkit-box-align: center;
    align-items: center;
    -webkit-box-pack: start;
    justify-content: flex-start;
    margin-top: 0px !important;
    height: 105px;
}

.feature-wrapper:not(:last-child) .feature {
    border-bottom: 1px solid rgb(207, 207, 207);
}

.tab-container .features .feature-wrapper.active .feature {
    opacity: 1;
}

.feature .title {
    display: flex;
    flex-direction: column;
    font-size: 16px;
    -webkit-box-pack: center;
    justify-content: center;
    text-align: left;
    padding-left: 8px;
    padding-right: 8px;
    width: 100%;
    color: rgb(28, 28, 28);
    font-weight: 600;
}

.feature .description {
    display: flex;
    flex-direction: column;
    padding: 4px 8px;
    font-size: 14px;
    -webkit-box-pack: center;
    justify-content: center;
    text-align: left;
    width: 100%;
    color: rgb(33, 37, 41);
    font-weight: 300;
}

.tab-container .features .feature-wrapper.active .feature .title,
.tab-container .features .feature-wrapper.active .feature .description {
    color: #7c6553;
}

.two-columns-container {
    display: flex;
    flex-direction: row;
}

.two-columns-container .tensorboard {
    width: 50%;
    padding-right: 16px;
}

.two-columns-container .capture-video {
    width: 50%;
