}

.video-container.shadow {
    box-shadow: #9f9f9f 0 0 20px 0px;
    border-radius: 8px;
}

.video-container.shadow video {
    border-radius: 8px;
}

.video-container video {
    max-width: 100%;
}

.gif-container {
    display: flex;
    flex-direction: row;
    height: 210px;
    flex-wrap: wrap;
    height: fit-content;
    height: -moz-fit-content;
    justify-content: center;
    gap: 32px
}

.gif-container img {
    height: 210px;
}

.gif-container .capture-info-container {
    max-width: 500px;
    overflow: hidden;
}

.gif-container .capture-info-container .code-wrapper {
    padding-right: 40px;
}

.gif-container .capture-info-container .code-wrapper .btn-copy {
    top: 0 !important;
}

.gif-container .capture-info-container .code-wrapper {
    background-color: #fcf0d1;
    border-radius: 8px;
}

.gif-container .capture-info-container code {
    height: 35px;
