    margin-bottom: 15px;
}

.gif-container .capture-info-container code span {
    white-space: wrap;
}

.capture-video .feature-code .tab-container .features .feature-wrapper {
    margin-bottom: 15px;
}

.capture-video .feature-code .tab-container .features .feature-wrapper .feature {
    height: 226px;
    border: none;
}

.capture-video .feature-code .tab-container .features {
    height: unset;
}

.capture-video .code-section .code-container {
    height: unset;
}

.capture-video .code-section .code-container .code-block .code-wrapper code {
    height: 226px;
    margin-bottom: 15px;
}

/* RESPONSIVE */
@media (max-width: 1200px) {
    .code-section {
        max-width: 1140px;
    }
}

@media (max-width: 1070px) {
    .tab-container {
        width: 75%;
    }

    .blog-img-wrapper video {
        height: 200px;
    }

    .two-columns-container {
        flex-direction: column;
    }

    .code-flex {
        align-items: center;
    }

    .code-flex .code-wrapper {
        display: flex !important;
    }
}

@media (max-width: 992px) {
    .code-section {
        max-width: 960px;
    }
}

@media (max-width: 900px) {
    .feature-code.inner {
        flex-direction: column;
    }

    .tab-container {
        overflow: auto;
        width: 100%;
    }

    .tab-container .features {
        flex-direction: row;
        height: 160px;
        min-width: 1060px;
    }

    .feature-wrapper:not(:last-child) .feature {
        border-right: 1px solid #cfcfcf;
        border-bottom: unset;
    }

    .code-block code {
        opacity: 1 !important;
    }

    .code-wrapper {
        display: none;
    }

    .code-wrapper.active {
        display: block;
    }

    .code-container {
        height: 120px;
    }

    .code-section,
    .code-section>span {
        padding-left: 0;
    }

    .tab-container,
    .tab-container .features {
        padding-right: 0;
    }

    .feature-wrapper .feature {
        height: 142px;
    }

    .code-block>span {
        padding-left: 0;
    }

    .capture-video .feature-code .tab-container .features {
        width: 100%;
        min-width: unset;
    }
}

@media (max-width: 768px) {
    .code-section {
        max-width: 720px;
    }

    .blog-img-wrapper .video-wrapper {
        width: 100%;
    }

    .blog-img-wrapper.img {
        flex-direction: column;
        align-items: center;
    }
}

@media (max-width: 576px) {
    .code-section {
        max-width: 540px;
    }
}

@media (max-width: 475px) {

    .code-flex .video-container,
    .code-flex code {
        width: 300px !important;

    }
}

@media (max-width: 400px) {
    code {
        font-size: 12px !important;
    }

    .site-header .site-nav .nav a {
        padding: 0 6px;
    }

    .site-header .site-nav-logo {
        margin-right: 10px;
    }
}

